<template>
  <div id="app">
    <v-offline
      online-class="centered online"
      offline-class="centered offline"
      @detected-condition="amIOnline">
      <template v-slot:[onlineSlot] :slot-name="onlineSlot">
        <h3 style="font-size: 5em;">Online? {{ heartBorked }}</h3>
      </template>
      <template v-slot:[offlineSlot] :slot-name="offlineSlot">
        <h3 style="font-size: 5em;">Online? {{ heartBorked }}</h3>
      </template>
    </v-offline>
  </div>
</template>

<script>
import VOffline from 'v-offline';

export default {
  components: {
    VOffline,
  },
  data: () => ({
    onLine: null,
    onlineSlot: 'online',
    offlineSlot: 'offline',
  }),
  computed: {
    heartBorked() {
      if (this.onLine) {
        return '❤️';
      }
      return '💔';
    },
  },
  methods: {
    amIOnline(e) {
      this.onLine = e;
    },
  },
};
</script>
<style>
html, body {
    height: 100%;
}
body {
    margin: 0;
}
.centered {
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center;
}
.offline {
  height: 100vh;
  width: 100vw;
  background-color: #fc9842;
  background-image: linear-gradient(315deg, #fc9842 0%, #fe5f75 74%);
  text-align: center;
}
.online {
  height: 100vh;
  width: 100vw;
  background-color: #00b712;
  background-image: linear-gradient(315deg, #00b712 0%, #5aff15 74%);
  text-align: center;
}
</style>
